:root {
  --themed-dark: #011c40;
  --themed-light: #e3ecf6;
  --base-light: #f9fbfd;
  --logo-light: invert(92%) sepia(29%) saturate(18%) hue-rotate(161deg) brightness(101%) contrast(99%);
  --logo-dark: invert(7%) sepia(41%) saturate(4574%) hue-rotate(204deg) brightness(96%) contrast(99%);
  --heading-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  --navigation-shadow: drop-shadow(var(--heading-shadow));
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.reset-list {
  padding: 0;
}

.reset-list li::marker {
  color: transparent;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.page {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  font: 400 16px/24px "Montserrat", "Arial", sans-serif;
  color: var(--themed-dark);
}
@media (max-width: 767px) {
  .page {
    position: relative;
  }
}

.page__header {
  flex-shrink: 0;
  padding-top: 35px;
}
@media (max-width: 1023px) {
  .page__header {
    padding-top: 28px;
  }
}
@media (max-width: 767px) {
  .page__header {
    padding-top: 12px;
  }
}

.page__content {
  flex-grow: 1;
}

.page__footer {
  flex-shrink: 0;
}

.main-header {
  --bg-url: var(--header-bg-big-url);
  min-height: 650px;
  color: var(--base-light);
  background: var(--bg-url) no-repeat var(--themed-dark) center/cover;
  filter: var(--heading-shadow);
}
@media (max-width: 1023px) {
  .main-header {
    --bg-url: var(--header-bg-medium-url);
    min-height: 638px;
    filter: initial;
  }
}
@media (max-width: 767px) {
  .main-header {
    --bg-url: var(--header-bg-small-url);
    min-height: 500px;
  }
}
.main-header h1 {
  margin-top: 0;
  margin-bottom: 13px;
  font: 700 54px/66px "Montserrat", "Arial", sans-serif;
  text-shadow: var(--heading-shadow);
}
@media (max-width: 1023px) {
  .main-header h1 {
    font: 700 50px/61px "Montserrat", "Arial", sans-serif;
    text-shadow: initial;
  }
}
@media (max-width: 767px) {
  .main-header h1 {
    font: 700 36px/1.25 "Montserrat", "Arial", sans-serif;
  }
}
.main-header p {
  margin: 0;
  margin-bottom: 20px;
  font: 400 20px/1.2 "Montserrat", "Arial", sans-serif;
  text-shadow: var(--heading-shadow);
}
@media (max-width: 1023px) {
  .main-header p {
    font: 400 18px/22px "Montserrat", "Arial", sans-serif;
    text-shadow: initial;
  }
}
@media (max-width: 767px) {
  .main-header p {
    max-width: 21ch;
    font: 400 16px/1.4 "Montserrat", "Arial", sans-serif;
  }
}

@media (max-width: 767px) {
  .main-header__navigation--is-open {
    position: absolute;
    z-index: 1000;
    top: 0;
    right: 0;
    bottom: 0;
    min-width: 285px;
    height: 100%;
    overflow-y: auto;
    background-color: var(--base-light);
  }
}

.main-header__description {
  padding: 197px 95px 50px;
}
@media (max-width: 1023px) {
  .main-header__description {
    padding: 216px 0 50px;
  }
}
@media (max-width: 767px) {
  .main-header__description {
    padding-top: 126px;
  }
}

.main-navigation {
  --filter: var(--logo-light);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  filter: var(--navigation-shadow);
}
@media (max-width: 1023px) {
  .main-navigation {
    filter: initial;
  }
}
.main-navigation--is-open {
  --filter: var(--logo-dark);
}
@media (max-width: 767px) {
  .main-navigation--is-open {
    display: grid;
    row-gap: 22px;
    min-width: 285px;
    grid-template-columns: [full-start] minmax(42px, 1fr) minmax(13px, 1fr) [full-end];
    grid-auto-rows: min-content;
    align-items: start;
    padding-top: 15px;
    padding-left: 15px;
  }
}

.main-navigation__logo {
  margin-right: 5%;
}

.main-navigation__menu {
  max-width: 85%;
  margin: 0;
}
@media (max-width: 767px) {
  .main-navigation__menu {
    grid-column: full;
    order: 2;
    min-width: 100%;
  }
}

.main-navigation__toggle {
  margin-right: -7px;
}

.logo {
  display: grid;
  place-content: center;
  color: var(--base-light);
  filter: var(--filter);
}
@media (max-width: 1023px) {
  .logo {
    max-width: 38px;
  }
}
@media (max-width: 767px) {
  .logo {
    max-width: 42px;
    padding-top: 5px;
    padding-bottom: 5px;
    filter: var(--filter);
  }
}

.main-menu {
  --color: var(--base-light);
  display: flex;
  flex-wrap: wrap;
  column-gap: 33px;
  row-gap: 25px;
  align-items: center;
}
@media (max-width: 1023px) {
  .main-menu {
    column-gap: 23px;
    row-gap: 20px;
  }
}
@media (max-width: 767px) {
  .main-menu {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-menu--is-open {
    --color: var(--themed-dark);
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 8px;
    padding-left: 10px;
    padding-right: 10px;
  }
}
@media (max-width: 767px) {
  .main-menu--footer {
    display: flex;
    flex-direction: column;
    align-items: start;
    flex-wrap: nowrap;
    row-gap: 7px;
    min-width: 100%;
    margin: 0;
  }
}

.main-menu__link {
  --stroke: 0.17em;
  font: 500 16px/20px "Montserrat", "Arial", sans-serif;
  padding-bottom: calc(2.1 * var(--stroke));
  color: var(--color);
  background: conic-gradient(from 135deg at top, var(--color) 90deg, transparent 0) left 0 bottom var(--stroke)/calc(2 * var(--stroke)) var(--stroke) repeat-x, conic-gradient(from -45deg at bottom, var(--color) 90deg, transparent 0) left var(--stroke) bottom 0/calc(2 * var(--stroke)) var(--stroke) repeat-x;
  mask: linear-gradient(#000000 0 0) content-box, linear-gradient(#000000 0 0) 0/var(--_position, 0%) padding-box no-repeat;
  transition: 0.5s;
}
.main-menu__link:hover {
  --_position: 100%;
  color: var(--color);
}
@media (max-width: 1023px) {
  .main-menu__link {
    font: 500 14px/17px "Montserrat", "Arial", sans-serif;
  }
}
@media (max-width: 767px) {
  .main-menu__link {
    display: block;
    padding-top: 10px;
    padding-bottom: 10px;
    font: 500 18px/22px "Montserrat", "Arial", sans-serif;
  }
}
@media (max-width: 1023px) {
  .main-menu__link--footer {
    font: 500 12px/15px "Montserrat", "Arial", sans-serif;
  }
}
@media (max-width: 767px) {
  .main-menu__link--footer {
    padding-top: 5px;
    padding-bottom: 5px;
    font: 500 12px/15px "Montserrat", "Arial", sans-serif;
  }
}

.main-footer {
  --filter: var(--logo-light);
  background-color: var(--themed-dark);
  padding-top: 50px;
  padding-bottom: 50px;
}
@media (max-width: 1023px) {
  .main-footer {
    padding-top: 16px;
    padding-bottom: 16px;
  }
}
@media (max-width: 767px) {
  .main-footer {
    padding-top: 35px;
  }
}

.main-footer__navigation {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
@media (max-width: 767px) {
  .main-footer__navigation {
    flex-direction: column;
    align-items: start;
  }
}

.main-footer__logo {
  margin-right: 4%;
}
@media (max-width: 767px) {
  .main-footer__logo {
    margin: 0;
    margin-bottom: 16px;
  }
}

.main-footer__menu {
  max-width: 65%;
  margin: 0;
  margin-right: 30px;
}
@media (max-width: 767px) {
  .main-footer__menu {
    margin-bottom: 14px;
  }
}

.main-footer__social {
  max-width: 15%;
  margin: 0;
  margin-left: auto;
}
@media (max-width: 767px) {
  .main-footer__social {
    margin: 0;
    min-width: 100%;
  }
}

.social {
  display: flex;
  flex-wrap: wrap;
  align-content: start;
  gap: 27px;
}
@media (max-width: 1023px) {
  .social {
    gap: 23px;
  }
}

.social__link {
  display: grid;
  place-items: center;
  padding: 5px;
}
@media (max-width: 1023px) {
  .social__link {
    max-width: 16px;
  }
}

.about {
  display: grid;
  grid-template-columns: 45% 48.3%;
  column-gap: 6.7%;
  padding-top: 120px;
  padding-bottom: 166px;
}
@media (max-width: 1023px) {
  .about {
    grid-template-columns: 48% 48%;
    column-gap: 4%;
    padding-top: 91px;
    padding-bottom: 82px;
  }
}
@media (max-width: 767px) {
  .about {
    display: block;
    padding-top: 56px;
    padding-bottom: 85px;
  }
}

.about__description {
  padding-top: 56px;
}
@media (max-width: 1023px) {
  .about__description {
    padding-top: 0;
  }
}
.about__description h2 {
  margin: 0;
  margin-bottom: 25px;
  font: 600 28px/1.5 "Montserrat", "Arial", sans-serif;
}
@media (max-width: 1023px) {
  .about__description h2 {
    margin-bottom: 16px;
    padding-right: 24px;
    font: 600 26px/1.4 "Montserrat", "Arial", sans-serif;
  }
}
@media (max-width: 767px) {
  .about__description h2 {
    font: 600 24px/1.4 "Montserrat", "Arial", sans-serif;
  }
}
.about__description p {
  margin-top: 0;
  margin-bottom: 10px;
  font: 400 16px/1.5 "Montserrat", "Arial", sans-serif;
}

@media (max-width: 1023px) {
  .about__picture {
    padding-top: 9px;
  }
}
@media (max-width: 767px) {
  .about__picture {
    padding-top: 34px;
  }
}

.toggle {
  display: none;
  position: relative;
  appearance: none;
  width: 38px;
  height: 38px;
  justify-content: center;
  justify-self: end;
  padding: 5px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
@media (max-width: 767px) {
  .toggle {
    display: flex;
  }
}

.toggle__button {
  --bg-color: var(--base-light);
  --transition-duration: 300ms;
  position: absolute;
  top: 48%;
  display: block;
  width: 24px;
  height: 2px;
  background-color: var(--bg-color);
  transition-duration: var(--transition-duration);
}
.toggle__button::before {
  content: "";
  position: absolute;
  top: -6px;
  display: block;
  width: 24px;
  height: 2px;
  background-color: var(--bg-color);
  transition: transform var(--transition-duration) ease-out;
}
.toggle__button::after {
  content: "";
  position: absolute;
  top: 6px;
  display: block;
  width: 24px;
  height: 2px;
  background-color: var(--bg-color);
  transition: transform var(--transition-duration) ease-out;
}
.toggle__button--open {
  background-color: transparent;
}
.toggle__button--open::before {
  content: "";
  transform: rotateZ(45deg) scaleX(1.35) translate(3px, 3px);
}
.toggle__button--open::after {
  content: "";
  transform: rotateZ(-45deg) scaleX(1.35) translate(3px, -3px);
}

.advantages {
  --bg-url: var(--advantages-bg-big-url);
  position: relative;
  padding-top: 110px;
  padding-bottom: 120px;
}
@media (max-width: 1023px) {
  .advantages {
    padding-top: 90px;
  }
}
@media (max-width: 767px) {
  .advantages {
    padding-top: 57px;
  }
}
.advantages::after {
  content: "";
  position: absolute;
  inset: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: var(--bg-url) no-repeat center/cover;
  background-color: var(--themed-light);
  background-blend-mode: overlay;
  opacity: 0.9;
}
.advantages h2 {
  margin: 0;
  margin-bottom: 50px;
  font: 600 28px/1.5 "Montserrat", "Arial", sans-serif;
  text-align: center;
}
@media (max-width: 1023px) {
  .advantages h2 {
    font: 600 26px/1.5 "Montserrat", "Arial", sans-serif;
  }
}
@media (max-width: 767px) {
  .advantages h2 {
    margin-bottom: 30px;
    font: 600 24px/1.5 "Montserrat", "Arial", sans-serif;
  }
}
.advantages dl {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  grid-auto-rows: minmax(0, 1fr);
  gap: 3.5%;
  margin: 0;
}
@media (max-width: 1023px) {
  .advantages dl {
    gap: 4.4%;
  }
}
@media (max-width: 767px) {
  .advantages dl {
    grid-template-columns: minmax(0, 1fr);
    grid-auto-rows: initial;
    gap: 3.8%;
  }
}

.advantages__item {
  --color: var(--themed-dark);
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 30px 30px 45px;
  color: var(--color);
  background-color: var(--base-light);
}
@media (max-width: 1023px) {
  .advantages__item {
    padding: 23px 18px 19px;
  }
}
@media (max-width: 767px) {
  .advantages__item {
    padding: 20px 15px 19px 19px;
  }
}
.advantages__item::before {
  --icon-size: var(--advantages-icon-big-size);
  --icon-url: var(--advantages-icon-url);
  content: "";
  display: block;
  flex-shrink: 0;
  width: var(--icon-size);
  min-width: var(--icon-size);
  height: 32px;
  margin-bottom: 18px;
  background-color: var(--themed-dark);
  mask: var(--icon-url) no-repeat left top/contain;
}
@media (max-width: 1023px) {
  .advantages__item::before {
    --icon-size: var(--advantages-icon-medium-size);
    margin-bottom: 14px;
  }
}
@media (max-width: 767px) {
  .advantages__item::before {
    --icon-size: var(--advantages-icon-small-size);
    margin-bottom: 18px;
  }
}
.advantages__item dt {
  margin-bottom: 10px;
  font: 500 18px/1.5 "Montserrat", "Arial", sans-serif;
}
@media (max-width: 767px) {
  .advantages__item dt {
    max-width: 16ch;
  }
}
.advantages__item dd {
  margin: 0;
  font: 400 14px/1.4 "Montserrat", "Arial", sans-serif;
}

